<template>
  <!--  -->
  <div id="ShowItem" v-loading="loading">
    <div id="container" class="main"></div>
  </div>
</template>

<script>
// import { Graph, Shape, Addon, DataUri } from "@antv/x6";
import { Graph, Shape, Addon } from "@antv/x6";
import "@antv/x6-vue-shape";
// import { text } from "@antv/x6/lib/util/dom/text";
export default {
  data() {
    return {
      graph: "", //
      curNode: "", // 
      curProjectJson: "", //json
      loading: false, // 
      mapMsg: {}, //
      stencil: "", //
      // 
      colorList: [
        "#fc5c65",
        "#fd9644",
        "#fed330",
        "#26de81",
        "#2bcbba",
        "#45aaf2",
        "#4b6584",
        "#d1d8e0",
        "#b8e994",
        "#60a3bc",
        "#4a69bd",
        "#f8c291",
      ],
      // 
      shapeList: ["polygon", "circle", "rect"],
      nodeShape: [
        //
        {
          inherit: "polygon",
          width: 70, // Number width 
          height: 30, // Number height 
          attrs: {
            body: {
              fill: "#fc5c65",
              stroke: "#e77c8e",
              strokeWidth: 1,
              refPoints: "5,0 50,0 45,20 0,20",
            },
            label: {
              color: "#262626",
              fontSize: 10,
              // refX: 8,
              textAnchor: "middle",
              textWrap: {
                // text: "lorem ipsum dolor sit amet consectetur adipiscing elit",
                text: "",
                width: -20,
                height: "90%",
                ellipsis: true, // 
                breakWord: true, // 
              },
            },
          },
          ports: {
            // ...ports,//
          },
        },
        //
        {
          inherit: "rect",
          width: 70, // Number width 
          height: 30, // Number height 
          attrs: {
            body: {
              fill: "#fc5c65",
              stroke: "#e77c8e",
              strokeWidth: 1,
              rx: 20,
              ry: 26,
            },
            label: {
              color: "#262626",
              fontSize: 10,
              // refX: 8,
              textAnchor: "middle", //
              textWrap: {
                // text: "lorem ipsum dolor sit amet consectetur adipiscing elit",
                text: "",
                width: -10,
                height: "90%",
                ellipsis: true, // 
                breakWord: true, // 
              },
            },
          },
          ports: {
            // ...ports,//
          },
        },
        //
        {
          inherit: "rect",
          width: 70, // Number width 
          height: 30, // Number height 
          attrs: {
            body: {
              fill: "#fc5c65",
              stroke: "#e77c8e",
              strokeWidth: 1,
            },
            label: {
              color: "#262626",
              fontSize: 10,
              // refX: 50,
              textAnchor: "middle",
              textWrap: {
                // text: "lorem ipsum dolor sit amet consectetur adipiscing elit",
                text: "",
                width: -10,
                height: "90%", //
                ellipsis: true, // 
                breakWord: true, // 
              },
            },
          },
          ports: {
            // ...ports,//
          },
        },
      ],
    };
  },
  computed: {
    //
    projectJson() {
      return this.$store.state.projectManage.itemShow.projectJson;
    },
    // vuex
    curItemVuex() {
      return this.$store.state.projectManage.resultList.curItem;
    },
  },
  watch: {
    projectJson: {
      handler(newValue, oldValue) {
        this.graph.fromJSON(newValue);
        console.log(newValue);
        console.log(oldValue);
      },
      deep: true,
    },
    curItemVuex: {
      handler(newValue) {
        this.loading = true;
        this.$api
          .getProjectConfig({ id: newValue.id })
          .then((res) => {
            console.log(res);
            this.graph.clearCells();
            let projectJson = res.mapMsg.graph_config;
            if (projectJson.length) {
              this.graph.fromJSON(projectJson);
            } else {
              this.$message({
                showClose: true,
                type: "info",
                message: ",",
              });
            }

            this.loading = false;
            this.$store.commit("projectManage/setItemShow", {
              key: "runningConfig",
              value: res.mapMsg.running_config,
            });
          })
          .catch((err) => {
            this.loading = false;
            console.log(err);
          });
      },
    },
  },
  methods: {
    //
    setGraph() {
      const graph = new Graph({
        container: document.getElementById("container"),
        width: "100%",
        height: "100%",
        autoResize: true,
        background: {
          color: "white", // 
        },
        grid: {
          size: 10, //  .125rem
          visible: true, // 
        },
        // 
        connecting: {
          router: {
            name: "manhattan",
            args: {
              padding: 1,
            },
          },
          connector: {
            name: "rounded",
            args: {
              radius: 8,
            },
          },
          anchor: "center",
          connectionPoint: "anchor",
          allowBlank: false,
          snap: {
            radius: 20,
          },
          createEdge() {
            return new Shape.Edge({
              attrs: {
                line: {
                  stroke: "#A2B1C3",
                  strokeWidth: 2,
                  targetMarker: {
                    name: "block",
                    width: 12,
                    height: 8,
                  },
                },
              },
              zIndex: 0,
            });
          },
          validateConnection({ targetMagnet }) {
            return !!targetMagnet;
          },
        },
        // 
        panning: {
          enabled: true,
        },
        selecting: {
          enabled: true,
          multiple: true,
          rubberband: true,
          movable: true,
          showNodeSelectionBox: true,
          showEdgeSelectionBox: true,
          modifiers: "shift",
        },
        history: true, // 
        //
        mousewheel: {
          enabled: true,
          zoomAtMousePosition: true,
          modifiers: "ctrl",
          minScale: 0.5,
          maxScale: 3,
        },
        //
        resizing: {
          enabled: false, //
          minWidth: 20,
          minHeight: 10,
          maxWidth: 500,
          maxHeight: 500,
          orthogonal: true,
          restricted: false,
          autoScroll: true,
          preserveAspectRatio: false,
          allowReverse: true,
        },
        //
        interacting: {
          nodeMovable: false,
        },
      });
      this.graph = graph;
    },
    //
    setNode() {
      let graph = this.graph; //
      let mapMsg = this.mapMsg; //
      //   
      const ports = {
        groups: {
          top: {
            position: "top",
            attrs: {
              circle: {
                r: 4,
                magnet: true,
                stroke: "#5F95FF",
                strokeWidth: 1,
                fill: "#fff",
                style: {
                  visibility: "hidden",
                },
              },
            },
          },
          right: {
            position: "right",
            attrs: {
              circle: {
                r: 4,
                magnet: true,
                stroke: "#5F95FF",
                strokeWidth: 1,
                fill: "#fff",
                style: {
                  visibility: "hidden",
                },
              },
            },
          },
          bottom: {
            position: "bottom",
            attrs: {
              circle: {
                r: 4,
                magnet: true,
                stroke: "#5F95FF",
                strokeWidth: 1,
                fill: "#fff",
                style: {
                  visibility: "hidden",
                },
              },
            },
          },
          left: {
            position: "left",
            attrs: {
              circle: {
                r: 4,
                magnet: true,
                stroke: "#5F95FF",
                strokeWidth: 1,
                fill: "#fff",
                style: {
                  visibility: "hidden",
                },
              },
            },
          },
        },
        items: [
          {
            group: "top",
          },
          {
            group: "right",
          },
          {
            group: "bottom",
          },
          {
            group: "left",
          },
        ],
      };
      //   
      let classList = Object.keys(mapMsg);
      // 
      let groups = [];
      let nodeSet = {};
      const COLORLIST = this.colorList;
      const COLORLENGTH = this.colorList.length;
      // const SHAPELIST = this.shapeList;
      // const SHAPELENGTH = this.shapeList.length;

      //
      classList.forEach((item, index) => {
        let length = mapMsg[item].length;
        groups.push({
          title: item,
          name: item,
          graphHeight: Math.ceil(length / 2) * 60 || 130,
          layoutOptions: {
            rowHeight: 55,
          },
        });
        nodeSet[item] = [];
        let nodeShape = JSON.parse(JSON.stringify(this.nodeShape[index]));
        nodeShape.ports = {
          ...ports,
        };
        nodeShape.attrs.label.text = item;
        //
        Graph.registerNode(item, nodeShape, true);
        //
        mapMsg[item].forEach((nodeItem, nodeIndex) => {
          let initailData = {};
          let currentData = nodeItem["module_config"]["parameters"];
          Object.keys(currentData).forEach((dataKey) => {
            let currentDataItem = currentData[dataKey];
            if (Array.isArray(currentDataItem) && currentDataItem.length > 0) {
              initailData[dataKey] = currentDataItem[0];
            } else {
              initailData[dataKey] = currentDataItem;
            }
          });
          // name()
          initailData["name"] = nodeItem["name"];
          // 
          initailData["local_name"] = nodeItem["name"];

          let itemEx = graph.createNode({
            shape: item,
            attrs: {
              body: { fill: COLORLIST[nodeIndex % COLORLENGTH] },
              label: {
                text: nodeItem.name,
              },
            },
            // data: nodeItem["module_config"],
            // data: { name: nodeItem["name"] },
            data: { ...initailData },
          });
          nodeSet[item].push(itemEx);
        });
      });
      // console.log(groups);
      // console.log(nodeSet);

      // 
      const stencil = new Addon.Stencil({
        title: "",
        target: graph,
        //
        stencilGraphWidth: 240,
        stencilGraphHeight: 350,
        collapsable: true,
        groups: groups,
        layoutOptions: {
          columns: 2,
          columnWidth: 100,
          rowHeight: 60,
          resizeToFit: true,
        },
      });
      this.stencil = stencil;
      //
      // document.getElementById("stencil").appendChild(stencil.container);
      // 
      Object.keys(nodeSet).forEach((res) => {
        stencil.load(nodeSet[res], res);
      });
    },
    setNodeData() {
      const that = this;
      const graph = this.graph;
      //
      graph.on("node:click", ({ node }) => {
        that.curNode = node;
        //
        that.$store.commit("projectManage/setItemShow", {
          key: "currentNodeData",
          value: { ...node.data },
        });
      });
      //
      graph.on("edge:click", ({ edge }) => {
        //
        console.log(edge.data);
        that.$store.commit("projectManage/setItemShow", {
          key: "currentNodeData",
          value: { ...edge.data },
        });
      });
      //  { e, x, y, node, view }
      graph.on("blank:click", () => {
        that.curNode = "";
        that.$store.commit("projectManage/set_nodeParam_nodeType", "");
      });
    },
    getModules() {
      this.$api.getModules().then((res) => {
        console.log(res);
        this.mapMsg = res.mapMsg;
        this.setNode(); // 
      });
    },
    //
    getProjectData() {
      if (!this.curItemVuex.id) {
        return;
      }
      this.loading = true;
      this.$api
        .getProjectConfig({ id: this.curItemVuex.id })
        .then((res) => {
          // console.log(res);
          this.graph.clearCells();
          let projectJson = res.mapMsg.graph_config;
          if (projectJson.length) {
            this.graph.fromJSON(projectJson);
          } else {
            this.$message({
              showClose: true,
              message: ",",
            });
          }

          this.loading = false;
          this.$store.commit("projectManage/setItemShow", {
            key: "runningConfig",
            value: res.mapMsg.running_config,
          });
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },
  },
  created() {},
  mounted() {
    this.setGraph();
    this.getModules();
    this.setNodeData();
  },
  activated() {
    console.log(this.curItemVuex.id);
    this.getProjectData();
    console.log("");
  },
};
</script>

<style lang="scss" scoped>
#ShowItem {
  height: 100%;
  width: 100%;
  #container {
    height: 100% !important;
    width: 100% !important;
  }
}
</style>
