<template>
  <!--  -->
  <div class="status-table">
    <el-table
      :show-header="false"
      :highlight-current-row="false"
      :data="tableData2"
      style="width: 100%"
      :height="`${maxHeight}`"
    >
      <el-table-column prop="tag"> </el-table-column>
      <el-table-column prop="value"> </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    tableData: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      maxHeight: 200,
    };
  },
  computed: {
    tableData2() {
      return this.tableData.map((item) => {
        item.value = parseFloat(item.value).toFixed(4);
        return item;
      });
    },
  },

  // created() {},
  mounted() {
    // setTimeout(() => {
    //   this.maxHeight =
    //     $(".chart-show").height() >= 300 ? $(".chart-show").height() : 200;
    // }, 0);
  },
};
</script>

<style lang="scss" scoped>
.status-table {
  display: flex;
  height: 100%;
  width: 100%;
  .el-table {
    color: #ffff;
    font-size: 18px;
  }
}
::v-deep .el-table,
.el-table__expanded-cell {
  background-color: transparent;
}

::v-deep .el-table tr {
  background-color: transparent !important;
}
::v-deep .el-table__body td,
::v-deep .el-table__header th,
.el-table .cell {
  background-color: transparent;
}
::v-deep .el-table::before {
  //
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0px;
}
//
::v-deep .el-table tbody tr:hover > td {
  // background-color: transparent;
  background-color: #3c4a88;
}
</style>
