<template>
  <div class="status-echart" ref="echart">echart</div>
</template>

<script>
import echarts from "echarts";
export default {
  //props
  props: {
    chartData: {
      type: Object,
      default() {
        return {
          title: "",
          xData: [],
          series: [],
        };
      },
    },
    //
    //true 
    chartIndex: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    options() {
      return this.axisOption;
    },
    dataChart() {
      return this.chartData;
    },
  },
  watch: {
    chartData: {
      handler() {
        this.initChart();
      },
      deep: true,
      //   immediate: true,
    },
  },
  data() {
    return {
      //
      echart: null,
      //   
      axisOption: {
        title: {
          text: "",
          textStyle: {
            color: "#ffff",
          },
          left: 20,
        },
        // 
        grid: {
          left: "40",
          top: "40",
          bottom: "20",
          right: 20,
        },
        //
        tooltip: {
          //  
          trigger: "axis",
        },
        // // 
        // legend: {
        //   textStyle: {
        //     color: '#333',
        //   },
        // },
        //x
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: [],
          //
          axisLine: {
            lineStyle: {
              color: "#17b3a3",
            },
          },

          axisLabel: {
            color: "#ffff",
          },
        },
        //y
        yAxis: {
          type: "value",
          axisLine: {
            lineStyle: {
              color: "#17b3a3",
            },
          },
          axisLabel: {
            color: "#ffff",
          },
        },
        // 
        color: "#222222",
        //
        series: [
          {
            data: [],
            type: "line",
            areaStyle: {}, //
          },
        ],
      },
      colorList: [
        "#2ec7c9",
        "#b6a2de",
        "#5ab1ef",
        "#ffb980",
        "#d87a80",
        "#8d98b3",
        "#e5cf0d",
        "#97b552",
        "#95706d",
        "#dc69aa",
        "#07a2a4",
        "#9a7fd1",
        "#588dd5",
        "#f5994e",
        "#c05050",
        "#59678c",
        "#c9ab00",
        "#7eb00a",
        "#6f5553",
        "#c14089",
      ],
    };
  },
  methods: {
    //
    initChart() {
      console.log("initChart");
      //
      this.initChartData();
      if (this.echart) {
        this.echart.setOption(this.options);
      } else {
        this.echart = echarts.init(this.$refs.echart);
        this.echart.setOption(this.options);
      }
    },
    // 
    initChartData() {
      this.axisOption.title.text = this.chartData.title;
      this.axisOption.xAxis.data = this.chartData.xData;
      let colorListLength = this.colorList.length;
      this.axisOption.color = this.colorList[this.chartIndex % colorListLength];
      this.axisOption.series[0].data = this.chartData.yData;
    },
    // 
    resizeChart() {
      this.echart ? this.echart.resize() : "";
    },
  },
  mounted() {
    this.initChart();
    // 
    // 'resize' resizeChart
    window.addEventListener("resize", this.resizeChart);
  },
  destroyed() {
    window.removeEventListener("resize", this.resizeChart);
  },
};
</script>

<style lang="scss" scoped>
.status-echart {
  overflow: hidden;
  height: 100%;
  width: 100%;
}
</style>
