<template>
  <div>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="resource">
        <el-radio-group v-model="ruleForm.resource">
          <el-radio label="new"></el-radio>
          <el-radio label="inherit"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        label=""
        prop="inherit"
        v-if="ruleForm.resource === 'inherit'"
      >
        <el-select
          v-model="ruleForm.inherit"
          filterable
          remote
          clearable
          loading-text
          :remote-method="getRemoteData"
          placeholder=""
          @clear="clearInherit"
        >
          <el-option
            :label="project.name"
            :value="project.name"
            v-for="project in optionalProjects"
            :key="project.name"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="region">
        <el-select
          v-model="ruleForm.region"
          @change="changeProjectType"
          placeholder=""
        >
          <el-option
            :label="value"
            :value="value"
            :key="value"
            v-for="value in projectTypeListVuex"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="desc">
        <el-input type="textarea" v-model="ruleForm.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          ></el-button
        >
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      ruleForm: {
        name: "",
        region: "", //
        resource: "", //
        desc: "",
        inherit: "",
      },
      //
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 90,
            message: " 3  90 ",
            trigger: "blur",
          },
        ],
        region: [
          { required: true, message: "", trigger: "change" },
        ],
        type: [
          {
            type: "array",
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        resource: [
          { required: true, message: "", trigger: "blur" },
        ],
        desc: [{ required: true, message: "", trigger: "blur" }],
        inherit: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      //
      optionalProjects: [],
    };
  },
  computed: {
    //
    projectTypeListVuex() {
      return this.$store.state.projectManage.asideLeft.projectTypeList;
    },
    //
    projectTypeVuex() {
      return this.$store.state.projectManage.asideLeft.projectType.name;
    },
    //
    curNavList() {
      return this.$store.state.projectManage.resultList.curNavList;
    },
  },
  methods: {
    submitForm(formName) {
      const that = this;
      this.$refs[formName].validate((valid) => {
        //
        if (valid) {
          console.log(that.ruleForm);
          let params = {
            projectName: that.ruleForm.name,
            desc: that.ruleForm.desc,
            projectType: that.ruleForm.region,
            inherit: that.ruleForm.inherit,
          };
          this.addProject(params);
        } else {
          that.$message({
            showClose: true,
            type: "error",
            message: ``,
          });
          return false;
        }
      });
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //,
    addProject(params) {
      const that = this;
      this.$api
        .addProject(params)
        .then((res) => {
          console.log(res);
          if (res.code === 200) {
            that.$message({
              showClose: true,
              type: "success",
              message: "",
            });
            that.$store.commit("projectManage/set_dialogFrom", {
              ruleForm: { ...that.ruleForm, id: res.id },
              dialogFormVisible: false,
            });
            this.$refs["ruleForm"].resetFields();
          } else {
            that.$message({
              showClose: true,
              type: "error",
              message: `${res.message}`,
            });
          }
        })
        .catch((err) => {
          that.$message({
            showClose: true,
            type: "error",
            message: `${err.message}`,
          });
          console.log(err);
        });
    },
    // 
    getRemoteData(keyword) {
      console.log(keyword);
      let params = { pageNum: 1, pageSize: 12, keyword };
      this.$api
        .searchProject(params)
        .then((res) => {
          if (res.code === 200) {
            this.optionalProjects = res.mapMsg;
          } else {
            this.optionalProjects = [];
          }
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 
    clearInherit() {
      let projectType = this.ruleForm.region;
      if (projectType === this.projectTypeVuex) {
        this.optionalProjects = this.curNavList;
      } else {
        let params = {
          pageNum: 1,
          pageSize: 20,
          algriType: projectType,
        };
        this.$api
          .getProjectList(params)
          .then((res) => {
            if (res.code === 200) {
              this.optionalProjects = res.mapMsg;
            } else {
              this.optionalProjects = [];
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    //
    changeProjectType(currentValue) {
      let params = {
        pageNum: 1,
        pageSize: 20,
        algriType: currentValue,
      };
      this.$api
        .getProjectList(params)
        .then((res) => {
          if (res.code === 200) {
            this.optionalProjects = res.mapMsg;
          } else {
            this.optionalProjects = [];
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    console.log("dialogFrom");
  },
  mounted() {
    this.ruleForm.region = this.projectTypeVuex;
    this.optionalProjects = this.curNavList;
  },
  beforeDestroy() {
    console.log("dialogFrom,beforeDestroy");
  },
};
</script>

<style lang="scss" scoped></style>
