<template>
  <!--  -->
  <el-row id="ResultList">
    <el-col :span="24">
      <div class="result-search">
        <!--  @change="onChangeGetSearchResults" -->
        <el-input
          ref="inlineInput"
          class="inline-input"
          prefix-icon="el-icon-search"
          v-model="inputValue"
          :fetch-suggestions="querySearch"
          placeholder=""
          size="small"
          clearable
          @focus="switchSearchMode"
          @keypress.enter.native="getSearchResult"
          @clear="clearInputValue"
          @blur="restoreResultList"
        ></el-input>
      </div>
    </el-col>
    <el-col :span="24">
      <div class="result-show">
        <el-scrollbar ref="scrollList">
          <!--         -->
          <ul
            class="result-show-box"
            v-infinite-scroll="load"
            :infinite-scroll-disabled="true"
            :infinite-scroll-immediate="false"
            :infinite-scroll-delay="1000"
            @click="onCurItemClick"
          >
            <li
              class="result-show-box-item"
              v-for="(item, index) in navList"
              :class="curIndex == index ? 'is-active' : ''"
              :key="item + index"
              :data-index="index"
            >
              {{ item.name }}
            </li>
            <li class="result-show-box-loading" v-if="loading">...</li>
            <li class="result-show-box-noMore" v-if="disabled && !loading">
              
            </li>
          </ul>
        </el-scrollbar>
      </div>
    </el-col>
    <el-col :span="24">
      <div class="result-pagination">
        <el-pagination
          background
          :small="true"
          :pager-count="5"
          @current-change="handleCurrentChange"
          :current-page="currentPageNumber"
          :page-size="20"
          layout="prev, pager, next,jumper"
          :total="totalNumber"
        >
        </el-pagination>
      </div>
    </el-col>
  </el-row>
</template>

<script>
export default {
  data() {
    return {
      inputValue: "", //
      navList: [], //
      loading: false, //?
      curItem: "", //
      curIndex: 0, //
      //
      getProjectParams: {
        currentPage: 0,
        pageSize: 20,
        totalPage: 0,
        type: "",
        isNext: "",
      },
      disabled: false, // (false)
      cacheNavigationList: [], //
      cacheCurrentData: {
        curIndex: 0, //
        curItem: [], // 
        navList: [], // navList
        disabled: false,
        getProjectParams: "",
      },

      timer: null, //
      searchSwitch: false, //
      // searchParams: { keyword: "", pageSize: 20, pageNum: 1 },
      currentPageNumber: 1, // 
      totalNumber: 1, //
    };
  },
  computed: {
    // 
    selectedTypeVuex() {
      return this.$store.state.projectManage.asideLeft.projectType;
    },
    //
    curNavList() {
      return this.navList;
    },
    //
    isDelectVuex() {
      return this.$store.state.projectManage.resultList.isDelect;
    },
    //
    ruleFormVuex() {
      return this.$store.state.projectManage.dialogFrom.ruleForm;
    },
    // 
    currentTag() {
      return this.$store.state.NavigationPage.compTag.currentTag;
    },
  },
  watch: {
    // 
    inputValue: {
      handler() {
        this.disabled = false; //
        this.getProjectParams.currentPage = 0; //
      },
    },
    navList: {
      handler(newValue) {
        this.$store.commit("projectManage/setResultList", {
          key: "curNavList",
          value: [...newValue],
        });
      },
    },
    // 
    selectedTypeVuex: {
      handler(newValue) {
        console.log(newValue);
        this.getProjectParams.type = newValue.type;
        this.getProjectParams.currentPage = 1;
        this.curItem = ""; //
        this.curIndex = 0; //
        this.currentPageNumber = 1;
        this.navList = [];
        this.getProjectList(newValue.type, 1);
        this.disabled = false;
      },
      deep: true,
    },
    //  
    curItem: {
      handler(newVal) {
        let curItem = { ...newVal };
        let curIndex = parseInt(this.curIndex);
        this.$store.commit("projectManage/set_resultList_curItem", {
          curItem,
          curIndex,
        });
        console.log("");
      },
      deep: true,
    },
    //
    isDelectVuex: {
      handler(newVal) {
        if (newVal === true) {
          this.navList.splice(this.curIndex, 1);
          this.$store.commit("projectManage/delect_resultList_isDelect", false);
          let length = this.navList.length;
          if (length > 0 && this.curIndex < length) {
            this.curItem = this.navList[this.curIndex];
          } else {
            this.curIndex = length - 1;
            this.curItem = this.navList[this.curIndex];
          }
          //20,,
          // if (this.navList.length < 20) {
          //   this.load();
          // }

          console.log("");
        }
      },
    },
    //
    ruleFormVuex: {
      handler(newValue) {
        console.log(newValue);
        let item = {
          name: newValue.name,
          desc: newValue.desc,
          id: newValue.id,
          type: newValue.type,
        };
        this.navList.splice(0, 0, item);
        this.curIndex += 1;
      },
      deep: true,
    },
    currentTag: {
      handler(newValue) {
        if (newValue.routeName === this.$route.name) {
          this.curIndex = this.navList.findIndex(
            (item) => item.name === newValue.itemName
          );
          this.curItem = this.navList[this.curIndex];
        }
      },
      deep: true,
    },
  },
  methods: {
    handleSelect(key, keyPath) {
      // let  path=this.navList[key]
      //   this.$router.push({
      //     name: key,
      //   })
      console.log(key, keyPath);
    },
    querySearch(queryString, cb) {
      let results = [
        { value: "", other: "" },
        { value: "", other: "" },
        { value: "", other: "" },
      ];
      //   let results = queryString
      //     ? restaurants.filter(this.createFilter(queryString))
      //     : restaurants
      //  callback 
      cb(results);
    },
    //
    searchSelect(e) {
      console.log(e);
      this.navList.unshift({
        index: "2",
        navRouter: "DataManage",
        title: "",
        icon: "el-icon-document",
      });
    },
    //
    load() {
      if (this.searchSwitch) {
        this.searchLoad();
      } else {
        this.classifyLoad();
      }
    },
    //
    classifyLoad() {
      console.log("******************classifyLoad****************");
      this.loading = true;
      // this.disabled = true;
      this.getProjectParams.currentPage += 1;
      let pageNum = this.getProjectParams.currentPage;
      let pageSize = this.getProjectParams.pageSize;
      let type = this.selectedTypeVuex.type;
      this.$api
        .getProjectList({
          pageNum,
          pageSize,
          algriType: type,
        })
        .then((res) => {
          // console.log(res);
          if (res.code == 200) {
            this.disabled = !res.isNext;
            // this.getProjectParams.currentPage = res.currentPage + 1;
            this.getProjectParams.isNext = res.isNext;
            this.navList.push(...res.mapMsg);
            this.loading = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //
    searchLoad() {
      console.log("********************************");
      let newValue = this.inputValue;
      this.getProjectParams.currentPage += 1;
      let pageNum = this.getProjectParams.currentPage;
      let pageSize = this.getProjectParams.pageSize;
      this.$api
        .searchProject({ keyword: newValue, pageSize, pageNum })
        .then((res) => {
          console.log(res);
          if (res.code === 200) {
            this.disabled = !res.isNext;
            this.navList.push(...res.mapMsg);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //
    onCurItemClick(e) {
      console.log(e.target.className);
      console.log(/result-show-box-item/.test(e.target.className));
      if (/result-show-box-item/.test(e.target.className)) {
        console.log(e.target.dataset.index);
        this.curIndex = parseInt(e.target.dataset.index);
        this.curItem = this.navList[this.curIndex];
        let tagData = {
          itemName: this.curItem.name,
          routeName: this.$route.name,
          pageIndex: "",
          icon: "",
        };
        this.$store.commit("NavigationPage/setCompTagTags", tagData);
      }
    },
    //
    delectCurProject() {
      this.navList.splice(this.curIndex, 1);
    },
    // 
    getProjectList(type = "", pageNum) {
      let param = { pageNum, pageSize: 20, algriType: type };
      // let param = { pagenumber: 1, page_size: 20, algriType: type };
      // this.getProjectParams.type = type;
      this.$api
        .getProjectList(param)
        .then((res) => {
          // console.log(res);
          this.totalNumber = 20 * res.totalPages;
          this.navList = res.mapMsg;
          // this.navList.push(...res.mapMsg);

          this.$store.commit("projectManage/set_resultList_curItem", {
            curItem: res.mapMsg[0],
            curIndex: 0,
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //
    clearInputValue() {
      console.log("");
      this.$refs["inlineInput"].blur();
    },
    //, 
    switchSearchMode() {
      // 
      if (this.inputValue === "") {
        this.cacheCurrentData.navList = [...this.navList];
        this.cacheCurrentData.curIndex = this.curIndex;
        this.cacheCurrentData.curItem = this.curItem;
        this.cacheCurrentData.disabled = this.disabled;
        this.cacheCurrentData.getProjectParams = { ...this.getProjectParams };
        //
        this.getProjectParams = {
          currentPage: 1,
          pageSize: 20,
          totalPage: 0,
          type: "",
          isNext: "",
        };
        this.disabled = false;
        this.searchSwitch = true;
      }
    },
    // ,
    getSearchResult() {
      let newValue = this.inputValue;
      this.$api
        .searchProject({ keyword: newValue, pageSize: 20, pageNum: 1 })
        .then((res) => {
          if (res.code === 200) {
            // 
            this.navList = [];
            this.navList = [...res.mapMsg];
          } else {
            this.$message({
              type: "error",
              message: "",
              showClose: true,
            });
          }
        });
    },
    //,
    restoreResultList() {
      if (this.inputValue === "") {
        this.navList = [...this.cacheCurrentData.navList];
        this.curIndex = this.cacheCurrentData.curIndex;
        this.curItem = this.cacheCurrentData.curItem;
        this.disabled = this.cacheCurrentData.disabled;
        this.getProjectParams = { ...this.cacheCurrentData.getProjectParams };
        this.searchSwitch = false;
      }
    },
    handleCurrentChange(val) {
      this.currentPageNumber = val;
      this.getProjectList(this.selectedTypeVuex.type, val);
      console.log(`: ${val}`);
    },
  },
  created() {
    this.getProjectList(this.selectedTypeVuex.type, 1);
  },
};
</script>

<style lang="scss" scoped>
#ResultList {
  height: 100%;
  width: 100%;
  background-color: rgba(60, 88, 129, 1);
  .result-search {
    height: 40px;
    line-height: 40px;
  }
  .result-show {
    height: calc(100vh - 100px - 120px);
    width: 100%;
    // white-space: nowrap;
    &-box {
      width: 100%;
      height: 100%;
      margin-bottom: 30px;
      &-item {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        height: 40px;
        width: 100%;
        font-size: 12px;
        color: #ffff;
        // background: aliceblue;
        background-color: rgba(60, 88, 129, 1);
        line-height: 40px;
        text-align: center;
        margin: 10px 0;
      }
      &-item:hover {
        background-color: rgb(150, 205, 241);
      }
      .is-active {
        background-color: rgb(18, 136, 214);
      }
      &-loading {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        height: 40px;
        width: 100%;
        line-height: 40px;
        text-align: center;
        margin: 10px 0;
        background-color: rgb(35, 173, 17);
      }
      &-noMore {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        height: 40px;
        width: 100%;
        line-height: 40px;
        text-align: center;
        margin: 10px 0;
        background-color: rgb(159, 175, 17);
      }
    }
    .el-scrollbar {
      height: 100%;
    }
  }
  .result-pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    width: 100%;
    ::v-deep .el-pagination__jump {
      margin-left: 0px;
      color: #ffff;
    }
  }
}
</style>

<style lang="scss">
//
.el-scrollbar__wrap {
  overflow: scroll;
  width: calc(100% + 20px);
  height: calc(100% + 20px);
}
</style>
