<template>
  <el-menu
    default-active="0"
    class="el-menu-vertical-demo"
    @select="handleSelect"
    :collapse="false"
  >
    <el-menu-item
      :index="index + ''"
      v-for="(item, index) in navList"
      :key="index"
    >
      <i :class="iconList[index % 4].icon"></i>
      <span slot="title">{{ item.name }}</span>
    </el-menu-item>
  </el-menu>
</template>

<script>
export default {
  data() {
    return {
      isCollapse: true,
      navList: [
        // {
        //   name: "",
        //   type: "mbfx",
        // },
        // {
        //   name: "",
        //   type: "sjyc",
        // },
        // {
        //   name: "",
        //   type: "ycfx",
        // },
      ],
      iconList: [
        {
          index: "1",
          keyPath: "Navigation",
          title: "",
          icon: "el-icon-menu",
        },
        {
          index: "2",
          keyPath: "DataManage",
          title: "",
          icon: "el-icon-document",
        },
        {
          index: "3",
          keyPath: "TagImage",
          title: "",
          icon: "el-icon-setting",
        },
        {
          index: "4",
          keyPath: "TagImage",
          title: "",
          icon: "el-icon-setting",
        },
      ],
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      key = parseInt(key);
      let httpData = { ...this.navList[key] };
      this.$store.commit("projectManage/set_asideLeft_projectType", httpData);
    },
    getProjectType() {
      this.$api
        .getProjectType()
        .then((res) => {
          // console.log(res);
          if (res.code === 200) {
            //
            this.$store.commit("projectManage/setAsideLeft", {
              key: "projectTypeList",
              value: [...res.project_type],
            });
            this.navList = res.project_type.map((item) => {
              return { name: item, type: item };
            });
            let httpData = { ...this.navList[0] };

            this.$store.commit(
              "projectManage/set_asideLeft_projectType",
              httpData
            );
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    this.getProjectType();
  },
  mounted() {},
};
</script>

<style lang="scss" scoped>
.el-menu {
  height: 100%;
  width: 100%;
}
</style>
