<template>
  <div class="status-progress">
    <div id="title"></div>
    <el-progress
      type="circle"
      :percentage="percentage"
      :stroke-width="16"
      :color="colors"
    ></el-progress>
  </div>
</template>

<script>
export default {
  props: {
    percentage: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      // percentage: 70,
      colors: [
        { color: "#C280FF", percentage: 20 },
        { color: "#e6a23c", percentage: 40 },
        { color: "#5cb87a", percentage: 60 },
        { color: "#1989fa", percentage: 80 },
        { color: "#C280FF", percentage: 100 },
      ],
    };
  },
  methods: {},
};
</script>

<style lang="scss" scoped>
.status-progress {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;

  #title {
    height: 40px;
    width: 100%;
    line-height: 40px;
    // background-color: red;
    text-align: center;
    font-size: 20px;
    color: #ffff;
  }
  .el-progress {
    height: calc(100% - 40px);
    // width: 100%;
    padding-top: 20px;
  }
  ::v-deep .el-progress__text {
    color: #ffff;
    font-size: 24px !important;
  }
}
</style>
